@import '~antd/dist/antd.css';
@import '~react-quill/dist/quill.snow.css';

body {
    margin: 0;
    padding: 0;
    font-family: "Hind", sans-serif;
    font-size: 16px;
    line-height: 24px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.ant-layout {
    background: #fff;
}

.login-form-forgot {
    float: right;
}
.login-form-button {
    width: 100%;
}

.login-logo {
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-logo img {
    height: 60px;
}

/*front end*/

.front-header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #fff !important;
    padding: 0 !important;
    height: 58px !important;
    line-height: 58px !important;
    /*border-bottom: 1px solid #1890ff !important;*/
    z-index: 9;
    /*box-shadow: 0 1px 0 rgba(0, 135, 252, 0.1), 0 1px 6px rgba(0, 135, 252, 0.1);*/
    box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.06);
    pointer-events: auto;
}

.header-logo {
    height: 50px
}

.header-content {
    padding: 0 50px;
    display: flex;
    justify-content: space-between;
}

.header-content .ant-menu {
    background: transparent !important;
}

.ant-menu-horizontal > .ant-menu-submenu-open {
    border: none;
}

.header-content .ant-menu-horizontal {
    border: none !important;
}

.header-content .ant-menu-horizontal > .ant-menu-item,
.header-content .ant-menu-horizontal > .ant-menu-submenu {
    position: relative;
    top: 8px;
    float: left;
    border-bottom: none;
}

/*social wrapper*/

.sociallogin-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 100px;
    justify-content: center;
    align-items: center;
}

.sociallogin-container .fbbtn {
    background-color: #29487d;
    color: #fff;
}


.sociallogin-container h4 {
    margin: 10px 0;
}


.content {
    padding-top: 100px;
    padding-left: 50px;
    padding-right: 50px;
    min-height: 100vh;
    background: #fff;
}

.main-logo {
    height: 32px;
    background: rgba(255, 255, 255, .2);
    margin: 16px;
}

.special-menu {
    display: block;
    text-align: center;
}

.special-menu h4 {
    font-weight: bold;
    color: #8FB98B;
    font-size: 16px;
}

.catslink {
    background-color: #D7EDD3;
    display: block;
    margin: 5px;
}

.catslink:hover {
    background-color: rgb(50, 57, 49);
}

.catslink a {
    padding: 20px;
    display: block;
    text-align: center;
}

.catslink span {
    display: block;
    color: #8FB98B;
    font-size: 13px;
    font-weight: bold;
}

.catsheading {
    border-radius: 20px 20px 0 0;
    padding: 5px 20px;
    background-color: #8FB98B;
    margin-left: 5px;
    margin-bottom: 10px;
    text-align: center;
    color: #fff;
    font-weight: bold;
    display: inline-block;
}

.csinput {
    margin: 10px 0;
}

.cscrwrapper {
    overflow: hidden;
    margin-bottom: 50px;
    margin-top: 20px;
}

.slick-slide {
    text-align: center;
    height: 160px;
    line-height: 160px;
    background: #364d79;
    overflow: hidden;
    display: inline-block;
    z-index: 0;
}

.slick-slide:hover {
    -webkit-box-shadow: 1px 1px 16px 3px rgba(153, 153, 153, 1);
    -moz-box-shadow: 1px 1px 16px 3px rgba(153, 153, 153, 1);
    box-shadow: 1px 1px 16px 3px rgba(153, 153, 153, 1);
    z-index: 1;
}

.slick-slide h3 {
    color: #fff;
}

.col-inner-pd {
    padding: 60px 10px;
}

.col-inner-pd h3 {
    text-align: center;
}

.longbtn {
    width: 100%;
    font-size: 30px;
    height: 60px;
    margin: 40px 0;
    line-height: 60px!important;
}

.footer-menu h4 {
    font-size: 18px;
    font-weight: bold;
}

.footer-menu ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.footer-menu ul li a {
    font-size: 16px;
}

.footer-menu .social-icons li a i {
    font-size: 30px;
}

.footer-menu .social-icons li {
    position: relative;
    display: inline-block;
    margin-right: 10px;
}

.footer-logo {
    height: 80px;
}

.ant-layout-footer .footer-menu h4,
.ant-layout-footer .footer-menu.centered {
    text-align: center;
}

.header-content .ant-menu-horizontal > .ant-menu-item-selected,
.header-content .ant-menu-horizontal > .ant-menu-item:hover {
    border-bottom: none;
}


/*admin*/


.header-admin {
    height: 48px !important;
    background-color: #ffffff !important;
    padding: 0 20px 0 20px !important;
    position: relative;
    white-space: nowrap;
    border-bottom: 1px solid #d2d2d2;
    z-index: 2;
    line-height: 48px !important;
}

.sidebar-admin {
    position: fixed;
    height: 100%;
    width: 256px !important;
    left: 0;
    bottom: 0;
    max-width: 256px !important;
    flex: 0 0 256px !important;
    background: #fff !important;
    border-right: 1px solid #e8e8e8;
}

.sidebar-admin .sidebarheader {
    height: 48px;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.sidebar-admin .sidebarheader img {
    width: 200px;
}

.sidebarmenu-wrapper {
    position: absolute;
    overflow-y: auto;
    overflow-x: hidden;
    top: 175px;
    right: 0;
    bottom: 10px;
    left: 0;
}

.sidebarmenu-wrapper-fix {
    top: 205px;
}

.hasSidebar {
    margin-left: 256px;
}

.mobiletoggle {
    visibility: hidden;
}

.pageheader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 70px;
}

.pagetitle {
    float: left;
    margin-right: 20px;
}

.user-profileinfo {
    padding: 20px;
    display: flex;
    flex-direction: column;
    position: relative;
    justify-content: center;
    border-bottom: 1px solid #dddedf;
}

.user-profileinfo h4 {
    letter-spacing: 1px;
}

.helper-bar {
    justify-content: flex-end;
    display: flex;
    padding: 10px 0;
}

.helper-bar button,
.helper-bar a {
    margin-right: 5px;
}


.row-extrapd {
    padding: 20px 10px;
}

.col-extrapd {
    padding: 0 10px;
}

.tbpd15 {
    padding: 15px 0;
}

.category-header {
    margin: 10px 0;
}

.categories-list {
    margin: 0;
    padding: 0;
    background: #d7edd3;
}

.categories-list li {
    padding-left: 30px;
    list-style: none;
    position: relative;
}

.categories-list li:hover {
    background: #c8dec4;
}

.categories-list li a {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 50px;
}

.categories-list li a img {
    width: 35px;
    margin-right: 10px;
}

.categories-list li a span {
    font-size: 16px;
    color: #000;
}

.all-products {
    padding: 0 10px;
}

.header-search {
    padding: 20px 10px;
}

.product-card {
    font-size: 14px;
    -webkit-font-feature-settings: "tnum";
    font-feature-settings: "tnum";
    font-variant: tabular-nums;
    line-height: 1.5;
    color: rgba(0,0,0,.65);
    box-sizing: border-box;
    margin-bottom: 10px;
    list-style: none;
    background: #d7edd3;
    border-radius: 2px;
    position: relative;
    -webkit-transition: all .3s;
    transition: all .3s;
    display: block;
    text-decoration: none;
}

.product-card:hover {
    color: rgba(0,0,0,.65);
    background: #c8dec4;
}

.product-card .cover {
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 10px;
}

.product-card .cover img {
    display: inline-block;
    vertical-align: top;
    max-width: 100%;
    max-height: 100%;
    overflow: hidden;
}

.product-desc {
    padding: 5px;
}

.product-desc h2 {
    font-size: 18px;
}

.productcard-footer {
    display: flex;
    justify-content: flex-end;
    padding: 5px;
}

.productcard-footer span {
    font-size: 16px;
}

.pdetails {
    padding-right: 10px;
    border-right: 1px solid #e9e7e7;
}

.postedinfo {
    font-size: 16px;
    padding: 10px 5px;
    margin-bottom: 10px;
}

.imageslider {
    height: 400px;
    position: relative;
}

.imageslider .slick-slide div {
    height: 400px;
}

.imageslider .slick-list img {
    height: 100%;
    width: auto!important;
}

.imageslider .imgthumb {
    display: flex!important;
    width: 100%;
    justify-content: center;
}

.imageslider .imgthumb li {
    position: relative;
    width: 60px;
    list-style: none;
    padding: 5px;
}

.imageslider .imgthumb img {
    width: 100%;
}

.item-price {
    margin: 1em;
}

.ui-price {
    position: relative;
    padding: .4rem 0;
    overflow: hidden;
}

.ui-price:before {
    content: '';
    position: absolute;
    z-index: 5;
    top: 0;
    left: -5px;
    bottom: 0;
    width: 10px;
    background: 0 0;
    background: -webkit-radial-gradient(50% 60%,rgba(0,0,0,.3),rgba(0,0,0,0));
    background: radial-gradient(50% 60%,rgba(0,0,0,.3),rgba(0,0,0,0));
    background-repeat: no-repeat;
}

.ui-price-tag {
    padding: .6rem 2.5rem .6rem 1.5rem;
    -webkit-box-shadow: 0 3px #e7edee;
    box-shadow: 0 3px #e7edee;
}
.ui-price-tag {
    position: relative;
    z-index: 1;
    display: inline-block;
    color: #2f3432;
    background: #d7edd3;
    border: solid 1px #c8dec4;
    border-width: 1px 0;
}

.ui-price-tag .r1, .ui-price-tag .r2, .ui-price-tag .r3 {
    content: '';
    border-top: solid 1.44rem transparent;
    border-bottom: solid 1.44rem transparent;
}
.ui-price-tag .r1 {
    top: -1px;
    border-right: solid 1.44rem #bed4ba;
}

.ui-price-tag .r2 {
    top: 0;
    border-top-width: 1.368rem;
    border-right: solid 1.368rem #e7edee;
    border-bottom-width: 1.368rem;
}

.ui-price-tag .r1, .ui-price-tag .r2, .ui-price-tag .r3 {
    content: '';
    border-top: solid 1.44rem transparent;
    border-bottom: solid 1.44rem transparent;
}

.ui-price-tag .r1, .ui-price-tag .r2, .ui-price-tag .r3 {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 0;
}

.ui-price-tag .r3 {
    top: 4px;
    border-top-width: 1.224rem;
    border-right: solid 1.224rem #fff;
    border-bottom-width: 1.224rem;
}

.ui-price-tag .amount {
    font-weight: bold;
}

.productdesc {
    margin-top: 70px;
    border-top: 1px solid #cac9c9;
    padding: 10px 0;
}

.product-title {
    font-size: 30px;
    line-height: 70px;
    font-weight: 500;
    margin-top: 19px;
    color: #191919;
}

.product-price {
    font-size: 30px;
    line-height: 40px;
    font-family: "Hind", sans-serif;
    font-weight: 500;
    color: #2879fe;
}

.price-content {
    font-size: 30px;
    line-height: 30px;
    font-family: "Hind", sans-serif;
    font-weight: 500;
    color: #2879fe;
    letter-spacing: 0.03em;
    transition: all 0.2s linear;
    -ms-transition: all 0.2s linear;
    -webkit-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    padding: 10px 0;
    border-top: 1px solid #e9e7e7;
    border-bottom: 1px solid #e9e7e7;
    margin-bottom: 20px;
}

.productmeta {
    padding: 10px;
}

.productmeta span {
    font-size: 20px;
}

.productmeta i {
    font-size: 20px;
    margin-right: 10px;
}

.productmeta h3 {
    margin-bottom: 10px;
}


h1, h2, h3, h4, h5, h6 {
    display: block;
    position: relative;
    font-family: "Hind", sans-serif;
    color: #191919;
    margin-bottom: 0px;
}

.socialshare-icons {
    background: #f7f8fa;
    padding: 12px 5px 10px;
    text-align: center;
    margin-bottom: 50px;
}

.socialshare-icons ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.socialshare-icons ul li {
    display: inline-block;
    margin-right: 5px;
}

.socialshare-icons ul li a {
    font-size: 35px;
    line-height: 26px;
    color: #d8d8d8;
    padding: 4px;
    transition: all 0.2s linear;
    -ms-transition: all 0.2s linear;
    -webkit-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
}

.socialshare-icons ul li a:hover {
    color: #191919;
}



.homespecial-icon {
    background: #f7f8fa;
    padding: 12px 5px 10px;
    text-align: center;
    margin-bottom: 50px;
}

.homespecial-icon ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.homespecial-icon ul li {
    display: inline-block;
    margin-right: 5px;
    margin-bottom: 10px;
}

.homespecial-icon ul li a {
    line-height: 40px;
    color: #d8d8d8;
    padding: 35px;
    transition: all 0.4s linear;
    -ms-transition: all 0.4s linear;
    -webkit-transition: all 0.4s linear;
    -o-transition: all 0.4s linear;
    border-radius: 100%;
    display: block;
    width: 200px;
    margin: 0 10px;
    border: 2px solid;
}

.homespecial-icon ul li a img {
    height: 90px;
    width: auto;
}

.homespecial-icon ul li a h4 {
    font-size: 13px;
}

.homespecial-icon ul li a:hover {
    background: #e9e9e9;
}

.related-products {
    padding-left: 50px;
    padding-right: 50px;
    background: #fff;
}

.related-cat-name {
    text-align: center;
}

/*editor*/

.form-control {
    width: 100%;
    margin-bottom: 10px;
}

.ql-snow .ql-editor pre.ql-syntax {
    background-color: #efefef !important;
    color: #090909 !important;
}

.ql-snow .ql-editor blockquote {
    border-left: 4px solid #0087fc;
}

.ql-container {
    min-height: 20em;
}

.ant-upload {
    width: 100%;
}

.ant-upload button span {
    font-size: 14px;
}

/*helpers*/

.col-inner {
    padding: 5px;
    font-size: 16px;
}

.deep-green {
    color: #8FB98B;
}

b, strong {
    font-weight: 700;
    line-height: inherit;
}

.img-responsive {
    width: 100%;
}

.footer-row {
    padding: 50px 50px;
    border-top: 5px solid #999;
    margin-top: 50px;
}

.mobilemenu {
    display: none;
}

.content-inner {
    padding: 0 10px;
}

.imgPreviewer {
    width: 100%;
    margin: 10px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.helper-bar {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    padding: 20px 0;
}

.helper-bar button, .helper-bar a {
    margin: 0 5px;
}

.mobilemenu .ant-menu-item .anticon {
    margin-right: 0;
}

.mb30 {
    margin-bottom: 30px;
}

.section-title {
    display: flex;
    width: 100%;
    justify-content: center;
}

.section-title h2 {
    margin: 20px 0;
    text-align: center;
    border-bottom: 1px solid #999;
    line-height: 50px;
    display: inline-block;
}

.csbtn {
    margin-top: 5px;
    color: #fff!important;
}

/*media queries*/


@media only screen and (max-width: 960px) {
    .desktopmenu {
        display: none;
    }

    .mobilemenu {
        display: block;
    }

    .footer-logo {
        height: 60px;
    }

    .header-content {
        padding: 0;
    }
}
